
/* Flex grid */
.row{
	display: flex;
	flex-wrap: wrap;
	margin-left: -30px;
}
.row + .row{
	margin-top: 30px;
}
.row > [class*='col-']{
	padding-left: 30px;
}
.row--collapse {
    margin-left: 0;
}
.row--collapse + .row--collapse {
    margin-top: 0;
}
.row--collapse > [class*="col-"] {
    padding-left: 0;
}

.col-12{
	width: 100%;
}
.col-11{
	width: 91.6666%;
}
.col-10{
	width: 83.3333%;
}
.col-9{
	width: 75%;
}
.col-8{
	width: 66.6666%;
}
.col-7{
	width: 58.33%;
}
.col-6{
	width: 50%;
}
.col-5{
	width: 41.66%;
}
.col-4{
	width: 33.333%;
}
.col-3{
	width: 25%;
}
.col-2{
	width: 16.666%;
}
.col-1{
	width: 8.3333%;
}


